import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/vacation'
import image1 from '@/assets/index/permit_1.png'
import image2 from '@/assets/index/permit_2.png'
import image3 from '@/assets/index/permit_3.png'
import image4 from '@/assets/index/permit_4.png'

export default {
  data () {
    return {
      permitData: [],
      tagImg: [image1, image2, image3, image4]
    }
  },
  mounted () {
    this.getPermit()
  },
  methods: {
    goToPage (item) {
      this.$store.commit('setMenuType', { data: item.type })
      this.$router.push({ name: item.state })
    },
    getPermit () {
      let token = this.$store.getters.getUser.token
      Indicator.open({
        text: '正在加载...',
        spinnerType: 'fading-circle'
      })
      API.GET_VACATION_PERMIT(this, { token }).then((rep) => {
        let result = rep.body
        Indicator.close()
        if (result.success) {
          this.permitData = result.data
        } else {
          MessageBox.alert(result.msg, '操作提示')
        }
      }, (rep) => {
        Indicator.close()
        Toast({
          message: '服务器异常',
          position: 'middle',
          duration: 1000
        })
      })
    }
  }
}



